import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';


// VKUI
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout'
import PromoBanner from '@vkontakte/vkui/dist/components/PromoBanner/PromoBanner'
import Button from '@vkontakte/vkui/dist/components/Button/Button'
// Icons
import Icon24LikeOutline from '@vkontakte/icons/dist/24/like_outline';
import Icon24Copy from '@vkontakte/icons/dist/24/copy';
import Icon24Like from '@vkontakte/icons/dist/24/like';
import Icon24CommentOutline from '@vkontakte/icons/dist/24/comment_outline';
import Icon24ReplyOutline from '@vkontakte/icons/dist/24/reply_outline';

import MySpinner from '../MySpinner'

import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader'
import HeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton'
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';

const booksinfo =  ({ id, this: love }) => (
    <Panel id={id} separator={false}>
         <PanelHeader separator={false} style={{background: '#f4f4f4'}} 
         left={<HeaderButton onClick={() => window.history.back()}><Icon24BrowserBack/></HeaderButton>}
         >
            {love.state.selected_book['genres'][0]['name']}
         </PanelHeader>
         {console.log(love.state.selected_book['cover'])}
         <div>
             <div className="image_bookinfo">
                 <img src={love.state.selected_book['cover']}/>
             </div>
             <div className="title_bookinfo">{love.state.selected_book['name']}</div>
             <div className="author_bookinfo">
             {love.state.selected_book['authors'].map(function(result, i) {
            return (
            <span key={i} onClick={() => love.goAuthor(result['id'])}>
                {result['first_name']} {result['last_name']}<br/>
            </span>
            )
    }) }
             </div>
         </div>
        
        {/* <div className="reyting_bookinfo">
            <div className="reyting_bookinfo_test">
                <div className="reyting_book"><Icon24Favorite width={20} height={20}/></div>
                <div className="reyting_book"><Icon24Favorite width={20} height={20}/></div>
                <div className="reyting_book"><Icon24Favorite width={20} height={20}/></div>
                <div className="reyting_book"><Icon24Favorite width={20} height={20}/></div>
                <div className="reyting_book"><Icon24Favorite width={20} height={20}/></div>
            </div>
        </div> */}
        
        <div className="reyting_bookinfo">
            <div className="reyting_bookinfo_test">
            <div>
                <div className="title_bookinfo_block">{love.state.selected_book['views']}</div>
                <div className="title_bookinfo_desc"></div>
            </div>
            <div className="hr"></div>
            <div>
                <div className="title_bookinfo_block">{love.state.selected_book['rating']}</div>
                <div className="title_bookinfo_desc"></div>
            </div>
            <div className="hr"></div>
            <div onClick={() => love.commentsGet(love.state.selected_book['id'])}>
                <div className="title_bookinfo_block">{love.state.selected_book['rated_count']}</div>
                <div className="title_bookinfo_desc"></div>
            </div>
            </div>
        </div>
        <br/>
        <div className="nonamedivs" >
        <Button className="normalbuttonfree" before={<Icon24Copy/>} onClick={() => { love.setState({popout: <MySpinner/>}); love.getReaderBook(love.state.selected_book['source']);}}></Button>
        <Button className="normalbuttontwo" onClick={() => love.setState({cover_to_share: love.state.selected_book['cover'], activeModal: "share_book", book_id_forshare: love.state.selected_book['id']})}><Icon24ReplyOutline/></Button>
        {
            love.state.is_favorite === true ?
            <Button className="normalbuttonone" onClick={() => love.deleteFavourite(love.state.selected_book['id'])}><Icon24Like/></Button>
            :
            <Button className="normalbuttonone" onClick={() => love.addFavourite(love.state.selected_book['id'])}><Icon24LikeOutline/></Button>
        }
        </div>
        <div className="modal_book">
            <div className="modal_block_title"></div>
            <div className="modal_block_desc">
            {love.state.selected_book['annotation']}
            </div>
        </div>
    </Panel>
);
export default booksinfo;