import React from 'react';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader'
import Header from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton'
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';
var month = ""
function TimeShow(time) {
    if(time === 0) {
        month = ""
    }
    if(time === 1) {
        month = ""
    }
    if(time === 2) {
        month = ""
    }
    if(time === 3) {
        month = ""
    }
    if(time === 4) {
        month = ""
    }
    if(time === 5) {
        month = ""
    }
    if(time === 6) {
        month = ""
    }
    if(time === 7) {
        month = ""
    }
    if(time === 8) {
        month = ""
    }
    if(time === 9) {
        month = ""
    }
    if(time === 10) {
        month = ""
    }
    if(time === 11) {
        month = ""
    }
}

function fix_time(time) {
    if(time < 10) {
        return (
            "0" + time
        )
    } else {
        return (
            time
        )
    }
}

const bookscomment = ({ id, this: love }) => (
    
    <Panel id={id}>
        <PanelHeader 
         left={<Header onClick={() => window.history.back()}><Icon24BrowserBack/></Header>}
         >
             
         </PanelHeader>
        {
            love.state.selected_book_comments['count'] === 0 ? 
            <div className="errorcomments" style={{marginTop: "270px"}}>
            ",   !"
            </div> : null
        }
        {
                        love.state.selected_book_comments['items'].map(function(result, i) {
                        return (
                           <Cell
        // onClick={
        //     () => love.openCommentAction(result['user']['first_name'], result['user']['last_name'], result['user']['id'], result['user']['photo_200']) 
        //     }
            href={"https://vk.com/id" + result['user']['id']}
            target="blank"
            component="a"
            key={i}
            size="l"
            before={<Avatar style={{marginTop: "5px"}} size={55} src={result['user']['photo_200']}/>}
        >
          <div className="namefa">{result['user']['first_name']} {result['user']['last_name']}</div>
           <div className="rating" style={{display: "flex"}}>
                <Icon24Favorite width={15} height={15}  style={result['rate'] >= 1 ? {color: "#fd7f10"} : null}/>
                <Icon24Favorite  width={15} height={15} style={result['rate'] >= 2 ? {color: "#fd7f10"} : null}/>
                <Icon24Favorite  width={15} height={15} style={result['rate'] >= 3 ? {color: "#fd7f10"} : null}/>
                <Icon24Favorite  width={15} height={15} style={result['rate'] >= 4 ? {color: "#fd7f10"} : null}/>
                <Icon24Favorite  width={15} height={15} style={result['rate'] >= 5 ? {color: "#fd7f10"} : null}/>
                <div className="commentstime">
            {new Date(result['date'] * 1e3).getDate()}
            {
            TimeShow(new Date(result['date'] * 1e3).getMonth())
            }
            {month}
            {new Date(result['date'] * 1e3).getFullYear()}
        ({fix_time(new Date(result['date'] * 1e3).getHours())}:{fix_time(new Date(result['date'] * 1e3).getMinutes())})</div>
        </div>
        <div className="commentcom">{result['comment']}</div>
      </Cell>
                    
                        )
                })
        }
        
    </Panel>
);

export default bookscomment;