import React from 'react';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import PromoBanner from '@vkontakte/vkui/dist/components/PromoBanner/PromoBanner';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout';
import HorizontalScroll from '@vkontakte/vkui/dist/components/HorizontalScroll/HorizontalScroll';

import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader'

const home = ({ id, this: love}) => (
    
    <Panel id={id}>
         <PanelHeader separator={false}>
             
         </PanelHeader>
         <Cell
        style={{paddingTop: "-20px"}}
        onClick={() => love.getProfile()}
        size="l"
        before={<Avatar style={{marginTop: "5px", marginLeft: "5px", background: "linear-gradient(to bottom, #ff5adf, #ff8d82)"}} size={55} src={love.state.photo_200}/>}
      >
          <div className="namehome">{love.state.firstname} {love.state.lastname}</div>
          
        <div className="nearnamehome">: {(Object.keys(love.state.historylenght).length)}, : {(Object.keys(love.state.favoritelength).length)}</div>
      </Cell>
      <div className="SearchMobile__input" >
                <input placeholder="" type="text" maxLength="100" value={love.state.searchQuery} onChange={love.inputChange}/>
            <div className="SearchMobile__to-filters-wrapper" onClick={() => love.setState({activeModal: 'search'})}>
                <span className="SearchMobile__to-filters"></span>
            </div>
            </div>
            {love.state.searchQuery.length === 0 ?
<div className="home_scroll">

    {love.state.is_group_member === 0 ?
        <div className="wrapper" onClick={() => love.Subscribe()}>
                <img className="wrapper" src="https://constructorus.ru/wp-content/uploads/2012/05/---.jpg"/>
        </div> : null}
    {love.state.bookstop.map(function(result, i) {
        return (
            <div className="wrapper" key={i} onClick={() => love.goBook(result['id'])}>
                <img className="wrapper" src={result['cover']}/>
            </div>
        )
        })}
</div>: null }
         <div>
       <br/>
        { love.state.radio === "0" ?
        love.state.searchQuery.length >= 3 ? 
        love.state.searchresult.length === 0 ? null :
        love.state.searchresult['books'].map(function(result, i) {
            return (
            <div key={i} >
                <div className="allbooks">
                    <img onClick={() => love.goBook(result["id"])} alt="" src={result['cover']}/>
                    <div onClick={() => love.goBook(result["id"])} className="content">
                    <div className="contenttitle">{result['name']}</div>
                    <div className="contentreyt">{result['rating']}</div>
                    <div className="contentauthorhome">
                    {result['authors'].map(function(result, i) {
            return (
            <span key={i}>
            {result['first_name']} {result['last_name']}
            </span>
            )
    }) }
                    </div>
                    <div className="contentviews">
                    : {result['genres'].map(function(result, i) {
            return (
            <span key={i}>
                {result['name']},
            </span>
            )
    }) }
    <div className="contentviews">
                        : {result['views']}
                    </div>
                    </div>
                </div>
                <div className="bottonfunctions" 
                onClick={() => {love.setState({
                    formodal_title: result['name'], 
                    formodal_id: result['id'], 
                    book_id_forshare: result['id'],
                    cover_to_share: result['cover']
                }); 
                love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
        </div>
        </div>
            )
    })  
        : 
        love.state.listbooks.map(function(result, i) {
            return (
            <div key={i} style={love.state.searchQuery.lenght >= 1 ? {marginTop: "-100px"} : null}>
                <div className="allbooks">
                    <img onClick={() => love.goBook(result["id"])} alt="" src={result['cover']}/>
                    <div onClick={() => love.goBook(result["id"])} className="content">
                    <div className="contenttitle">{result['name']}</div>
                    <div className="contentreyt">{result['rating']}</div>
                    <div className="contentauthorhome">
                    {result['authors'].map(function(result, i) {
            return (
            <span key={i}>
            {result['first_name']} {result['last_name']}
            </span>
            )
    }) }
                    </div>
                    <div className="contentviews">
                    : {result['genres'].map(function(result, i) {
            return (
            <span key={i}>
                {result['name']},
            </span>
            )
    }) }
    <div className="contentviews">
                        : {result['views']}
                    </div>
                    </div>
                </div>
                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], cover_to_share: result['cover'], formodal_id: result['id'], book_id_forshare: result['id']}); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
        </div>
        </div>
            )
    }) : null}   
     { love.state.radio === "0" ?
        love.state.searchQuery.length >= 3 ? 
        love.state.searchresult.length === 0 ?  <div className="errorsearch">   </div> :
        love.state.searchresult['authors'].map(function(result, i) {
            return (
                <div key={i} >
                <div className="allbooks">
                    <img onClick={() => love.goAuthor(result["id"])} alt="" src={result['photo']}/>
                    <div onClick={() => love.goAuthor(result["id"])} className="content">
    <div onClick={() => love.goAuthor(result["id"])} className="contenttitle">{result['first_name']} {result['last_name']}</div>
                    <div className="contentauthor">{result['activity']}</div>
                    <div className="contentviews">
                        : {result['lang'] === "ru" ? "" : ""}
                    </div>
                </div>
                <div className="bottonfunctions" onClick={() => {love.setState({authortitle: result['first_name'], help_id: result['id'], author_id_forshare: result['id'], authorlast: result['last_name']}); love.setActiveModal("author")}}><Icon24MoreVertical/></div>
        </div>
        </div>
            )
    }) 
        : 
        love.state.listbooks.map(function(result, i) {
            return (
            <div key={i} style={love.state.searchQuery.lenght >= 1 ? {marginTop: "-100px"} : null}>
                <div className="allbooks">
                    <img onClick={() => love.goBook(result["id"])} alt="" src={result['cover']}/>
                    <div onClick={() => love.goBook(result["id"])} className="content">
                    <div className="contenttitle">{result['name']}</div>
                    <div className="contentreyt">{result['rating']}</div>
                    <div className="contentauthorhome">
                    {result['authors'].map(function(result, i) {
            return (
            <span key={i}>
            {result['first_name']} {result['last_name']}
            </span>
            )
    }) }
                    </div>
                    <div className="contentviews">
                    : {result['genres'].map(function(result, i) {
            return (
            <span key={i}>
                {result['name']},
            </span>
            )
    }) }
    <div className="contentviews">
                        : {result['views']}
                    </div>
                    </div>
                </div>
                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], cover_to_share: result['cover'], formodal_id: result['id'], book_id_forshare: result['id']}); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
        </div>
        </div>
            )
    }) : null}   
     { love.state.radio === "1" ?
        love.state.searchQuery.length >= 3 ? 
        love.state.searchresult.length === 0 ?  <div className="errorsearch">   </div> :
        love.state.searchresult.map(function(result, i) {
            return (
            <div key={i} >
                <div className="allbooks" >
                    <img onClick={() => love.goBook(result["id"])} alt="" src={result['cover']}/>
                    <div onClick={() => love.goBook(result["id"])} className="content">
                    <div className="contenttitle">{result['name']}</div>
                    <div className="contentreyt">{result['rating']}</div>
                    <div className="contentauthorhome">{(result['authors']['0'].first_name)} {(result['authors']['0'].last_name)}</div>
                    <div className="contentviews">
                    :
                        {result['genres'].map(function(result, i) {
            return (
            <span key={i}>
                 {result['name']}
            </span>
            )
    }) }
    <div className="contentviews">
                        : {result['views']}
                    </div>
                    </div>
                </div>
                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], cover_to_share: result['cover'],  formodal_id: result['id'], book_id_forshare: result['id'] }); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
        </div>
        </div>
            )
    }) 
        : 
        love.state.listbooks.map(function(result, i) {
            return (
            <div key={i} style={love.state.searchQuery.lenght >= 1 ? {marginTop: "-100px"} : null}>
                <div className="allbooks">
                    <img onClick={() => love.goBook(result["id"])} alt="" src={result['cover']}/>
                    <div onClick={() => love.goBook(result["id"])} className="content">
                    <div className="contenttitle">{result['name']}</div>
                    <div className="contentreyt">{result['rating']}</div>
                    <div className="contentauthorhome">
                    {result['authors'].map(function(result, i) {
            return (
            <span key={i}>
            {result['first_name']} {result['last_name']}
            </span>
            )
    }) }
                    </div>
                    <div className="contentviews">
                    : {result['genres'].map(function(result, i) {
            return (
            <span key={i}>
                {result['name']},
            </span>
            )
    }) }
    <div className="contentviews">
                        : {result['views']}
                    </div>
                    </div>
                </div>
                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], cover_to_share: result['cover'], formodal_id: result['id'], book_id_forshare: result['id']}); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
        </div>
        </div>
            )
    }) : null}   
     { love.state.radio === "2" ?
        love.state.searchQuery.length >= 3 ? 
        love.state.searchresult.length === 0 ?  <div className="errorsearch">   </div> :
        love.state.searchresult.map(function(result, i) {
            return (
                <div key={i} >
                <div className="allbooks">
                    <img onClick={() => love.goAuthor(result["id"])} alt="" src={result['photo']}/>
                    <div onClick={() => love.goAuthor(result["id"])} className="content">
<div onClick={() => love.goAuthor(result["id"])} className="contenttitle">{result['first_name']} {result['last_name']}</div>
                    <div className="contentauthor">{result['activity']}</div>
                    <div className="contentviews">
                        : {result['lang'] === "ru" ? "" : ""}
                    </div>
                </div>
                <div className="bottonfunctions" onClick={() => {love.setState({ author_id_forshare: result['id'], help_id: result['id'], authortitle: result['first_name'], authorlast: result['last_name']}); love.setActiveModal("author")}}><Icon24MoreVertical/></div>
        </div>
        </div>
            )
    }) 
        : 
        love.state.listbooks.map(function(result, i) {
            return (
            <div key={i} style={love.state.searchQuery.lenght >= 1 ? {marginTop: "-100px"} : null}>
                <div className="allbooks">
                    <img onClick={() => love.goBook(result["id"])} alt="" src={result['cover']}/>
                    <div onClick={() => love.goBook(result["id"])} className="content">
                    <div className="contenttitle">{result['name']}</div>
                    <div className="contentreyt">{result['rating']}</div>
                    <div className="contentauthorhome">
                    {result['authors'].map(function(result, i) {
            return (
            <span key={i}>
            {result['first_name']} {result['last_name']}
            </span>
            )
    }) }
                    </div>
                    <div className="contentviews">
                    : {result['genres'].map(function(result, i) {
            return (
            <span key={i}>
                {result['name']},
            </span>
            )
    }) }
    <div className="contentviews">
                        : {result['views']}
                    </div>
                    </div>
                </div>
                <div className="bottonfunctions" onClick={() => {love.setState({formodal_title: result['name'], cover_to_share: result['cover'],  formodal_id: result['id'], book_id_forshare: result['id'] }); love.openModalSettings(result['id'])}}><Icon24MoreVertical/></div>
        </div>
        </div>
            )
    }) : null}
    {love.state.searchQuery.length >= 1 ?  null : 
                  love.state.listbooks_result.length >= 10 ? 
                  <Div>
                  <Button  size="xl" level="secondary" onClick={() => love.LoadBooks_Load()} style={{ marginRight: 8 }}> </Button>
                  </Div>
                  : null
                  }     
                  </div>
        {love.state.isShowPromo ?
                <FixedLayout vertical="bottom">
                    <PromoBanner onClose={() => love.setState({isShowPromo: false})} bannerData={love.state.promoBanner} />
                </FixedLayout> : null }
    </Panel>
);

export default home;