import React from 'react';
import Icon24MoreVertical from '@vkontakte/icons/dist/24/more_vertical';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader'
import HeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton'
import Button from '@vkontakte/vkui/dist/components/Button/Button'
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24ShareOutline from '@vkontakte/icons/dist/24/share_outline';

var months=[
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
 ];

function fix_month(month) {
    return months[month]
}

function fix_time(time) {
    if(time < 10) {
        return '0' + time
    } else {
        return time
    }
}

const profile = ({ id, this: love}) => (
    <Panel id={id} separator={false}>
       <PanelHeader 
         left={<HeaderButton onClick={() => love.goBack()}><Icon24BrowserBack/></HeaderButton>}
         >
         </PanelHeader>
         <div>
             <div className="image_bookinfo">
                 <img src={love.state.selected_book['cover']}/>
             </div>
             <div className="title_bookinfo">{love.state.name['first_name']} {love.state.name['last_name']} <span style={{fontWeight: "300"}}> </span><br/>{love.state.selected_book['name']}</div>
            <div className="buttons_news">
            <button className="button_special" id="reading" style={{marginRight: "10px"}} onClick={() => love.ReadBook()}>  </button>
            <button className="button_special" onClick={() => love.goBook(love.state.contex)}> </button>
            </div>
            <div>
                {love.state.specials.length === 0 ? <div className="error_special">     ,<br/> !</div> : 
                <div>
                <div className="title_special"> :</div>
                <br/>
                <div>
                    {love.state.specials.map(function(result, i) {
                        return (
                            <div key={i}>
                                <div className="people_special">
                                    <img className="people_special_img" src={result['user']['photo_200']}/>
                                    <div className="people_special_title">{result['user']['first_name']} {result['user']['last_name']}</div>
                                    <div className="people_special_desc">{fix_time(new Date(result['time'] * 1e3).getDate())} {fix_month(new Date(result['time'] * 1e3).getMonth())} {new Date(result['time'] * 1e3).getFullYear()}  {fix_time(new Date(result['time'] * 1e3).getHours())}:{fix_time(new Date(result['time'] * 1e3).getMinutes())}</div>
                                </div>
                            </div>
                        )
                    })}
                    <div>
                
                    </div>
                </div>
                </div>
                }
            </div>
         </div>
    </Panel>
);

export default profile;